import { useNavigate } from "react-router-dom";
import Navbar from "../../components/Navbar/navbar";
import About from "../About/About";
import Team from "../Team/Team";
import Footer from "../../components/Footer/Footer";
import v1 from '../../assets/v1.mp4';
import './LandingPage.css';


const LandingPage = () => {
    const navigate = useNavigate();
    return (
        <div className="landing-page">
            <a href="#nav" className="up-btn"><i class="fa-solid fa-caret-up"></i></a>
            <div className="overlay-back"></div>
            <video src={v1} className='back-video' autoPlay loop muted />
            <div className="home">
                <Navbar />
                <div className="home-main" id="home">
                    <h1>X-Ray Image Enchancer</h1>
                    <p>The DR (digital radiography) images may be obscured due to noise interference, improper exposure, and the excessive thickness of human tissues, resulting in indistinct edges and reduced contrast.</p>
                    <button onClick={() => navigate("/App")}>Try</button>
                </div>
            </div>
            <About />
            <Team />
            <Footer />
        </div>
    );
}

export default LandingPage