import { useEffect, useState } from "react"
import Spinner from "../Spinner/Spinner"

function Output(props) {
    const [load, setLoad] = useState();
    return (
        <div className='output-main'>

            <a className="left-btn" onClick={props.toggleOutput}><i class="fa-regular fa-circle-left"></i></a>
            {<div className='before'>
                <h2>Before</h2>
                <img src={props.in} alt='image' />
            </div>}

            {!props.load && <div className='after' id="after">
                <h2>After</h2>
                {/* {load && load} */}
                {props.out && props.out}
                {/* {document.getElementById('after').children.length > 1 ? null : <Spinner/>} */}
            </div>}
        </div>
    )
}

export default Output